<template>
    <el-container>
        <el-header>
            <layoutHeader></layoutHeader>
        </el-header>
        <el-container>
            <el-main>
                <div class="male-main">
                    <div class="ant-layout">
                        <div class="ant-layout ant-layout-has-sider">
                            <div class="ant-layout" style="padding: 20px 0;background: #fff;">
                                <div style="width: 1200px;margin: 0 auto;background: #fff;">
                                    <div style="margin-bottom: 10px;font-size: 16px;color: #56509F;">-</div>
                                    <div class="margin-top-20 color-6" style="padding: 0 20px;background: #F6F6F6;border: 1px solid #EBEBEB;line-height: 50px;">
                                        
                                    </div>

                                    <el-form direction="horizontal" label-width="120px" class="margin-top-20">
                                        <el-form-item label="">
                                            <span class="ant-form-text" id="userName" name="userName" style="font-size: 18px;">{{payData.name}}</span>
                                        </el-form-item>
                                        <el-form-item label="" >
                                            <div class="inline-block" style="font-size: 28px;margin-right: 20px;">
                                                <span class="color-warn">{{payData.price}}</span></div>
                                            <el-button type="default" html-type="button" @click="refreshPrice()"></el-button>
                                        </el-form-item>
                                        <el-form-item label="" >
                                            <div style='width:200px;height:200px;padding:10px;border:1px solid #eee;'>
                                                <img :src='payResult.qrcode' style='width:100%;' />
                                            </div>
                                            <span v-if='payData.id==20'>
                                                <a class="link" href='http://news.chaozhiedu.com/wj/jinpailubo.docx'>18</a></span>
                                            <span v-if='payData.id==10'>
                                                <a class="link" href='http://news.chaozhiedu.com/wj/vipshicao.docx'>18VIP</a></span>
                                            <span v-if='payData.id==9'>
                                                <a class="link" href='http://news.chaozhiedu.com/wj/gaoxiaoquzheng.docx'>18</a></span>
                                            <span v-if='payData.id==8'>
                                                <a class="link" href='http://news.chaozhiedu.com/wj/lingjichutezhao.docx'>18 </a></span>
                                        </el-form-item>
                                        <el-form-item label="">
                                            <img src="/static/images/20180316111851.png">
                                        </el-form-item>
                                    </el-form>
                                </div>
                            </div>
                        </div>
                    </div>

                    <el-dialog title="" width="800" :visible="wechatPayDialog" @cancel="wechatPayDialogCancle" wrap-class-name="vertical-center-modal">
                        <div class="text-center" style="font-size: 16px;">
                            <p>{{payData.name}}
                                <br/> {{payData.price}} </p>
                            <p>
                                <img :src='payResult.qrcode' style='width:200px;height:200px;' />
                            </p>
                            <p>
                                <svg class="icon inline-block" aria-hidden="true" style="font-size: 60px;margin-top: -20px;">
                                    <use xlink:href="#icon-weixinzhifu"></use>
                                </svg>
                            </p>
                            <p style="font-size: 18px;">010-51657777</p>
                        </div>
                        <div slot="footer">
                            <el-button key="cancel" type="ghost" size="large" @click="wechatPayDialogCancle"></el-button>
                        </div>
                    </el-dialog>

                    <el-dialog title="" width="800" :visible="yhkPayDialog" @cancel="yhkPayDialogCancle" wrap-class-name="vertical-center-modal">
                        <div style="font-size: 16px;">
                            <p style="font-weight: bold;">{{payState.data.title}}</p>
                            <pre v-html='payState.data.content'></pre>
                            <p style='color:orange;font-size:12px;'>{{payState.data.tip}}</p>
                        </div>
                        <div slot="footer">
                            <el-button key="cancel" type="ghost" size="large" @click="yhkPayDialogCancle"></el-button>
                        </div>
                    </el-dialog>

                    <el-dialog title="" width="800" :visible="alipayPayDialog1" @cancel="alipayPayDialogCancle1" wrap-class-name="vertical-center-modal">
                        <div class="clearfix" style="font-size: 16px;">
                            <div class="pull-left margin-left-20">
                                <i class="iconfont icon-warning" style="font-size: 100px;color: #FF4000;"></i>
                            </div>
                            <div class="pull-left margin-left-20" style="width: 450px;">
                                <div style="font-size: 24px;">
                                    
                                    <br/> 
                                </div>
                                <div class="margin-top-20" style="font-size: 14px;color: #FF4000;">
                                    
                                </div>
                                <div class="margin-top-20">
                                    <el-button type="primary"></el-button>
                                    <el-button type="default" style="margin-left: 10px;"></el-button>
                                </div>
                                <div class="margin-top-20" style="font-size: 18px;">010-51657777</div>
                            </div>
                            <!-- <div v-html="payResult.form"></div> -->
                        </div>
                        <div slot="footer">
                            <el-button key="cancel" type="ghost" size="large" @click="alipayPayDialogCancle1"></el-button>
                        </div>
                    </el-dialog>

                    <el-dialog title="" width="800" :visible="alipayPayDialog2" @cancel="alipayPayDialogCancle2" wrap-class-name="vertical-center-modal">
                        <div class="clearfix" style="font-size: 16px;">
                            <div class="pull-left margin-left-20">
                                <i class="iconfont icon-chenggong" style="font-size: 100px;color: #7ED321;"></i>
                            </div>
                            <div class="pull-left margin-left-20" style="width: 450px;">
                                <div style="font-size: 24px;">
                                    
                                </div>
                                <div class="margin-top-20" style="font-size: 14px;">
                                    {{payData.name}}
                                </div>
                                <div class="margin-top-20" style="font-size: 14px;color: #FF4000;">
                                    <el-button @click="$router.push('/male')" type="primary"></el-button>
                                    <el-button @click="$router.push('/courselist')" type="default" style="margin-left: 10px;"></el-button>
                                </div>
                                <div class="margin-top-20" style="font-size: 18px;">010-51657777</div>
                            </div>
                        </div>
                        <div slot="footer">
                            <el-button key="cancel" type="ghost" size="large" @click="alipayPayDialogCancle2"></el-button>
                        </div>
                    </el-dialog>

                    <el-dialog title="" width="800" :visible="alipayPayDialog3" @cancel="alipayPayDialogCancle3" wrap-class-name="vertical-center-modal">
                        <div class="clearfix" style="font-size: 16px;">
                            <div class="pull-left margin-left-20">
                                <i class="iconfont icon-warning" style="font-size: 100px;color: #FF4000;"></i>
                            </div>
                            <div class="pull-left margin-left-20" style="width: 450px;">
                                <div style="font-size: 24px;">
                                    
                                </div>
                                <div class="margin-top-20" style="font-size: 14px;">
                                    {{payData.name}}
                                </div>
                                <div class="margin-top-20">
                                    <el-button type="error"></el-button>
                                    <el-button type="default" style="margin-left: 10px;"></el-button>
                                </div>
                                <div class="margin-top-20" style="font-size: 18px;">010-51657777</div>
                            </div>
                        </div>
                        <div slot="footer">
                            <el-button key="cancel" type="ghost" size="large" @click="alipayPayDialogCancle3"></el-button>
                        </div>
                    </el-dialog>
                </div>
            </el-main>
        </el-container>
    </el-container>

</template>

<script>
let timer = 0;
import layoutHeader from "@/components/header.vue";

export default {
  name: "Pay",
  components: {
    layoutHeader
  },
  data() {
    return {
      labelCol: {
        span: 2
      },
      wrapperCol: {
        span: 22
      },

      payData: {},

      payStyle: "wechat",

      payState: {
        data: {}
      },

      wechatPayDialog: false,

      yhkPayDialog: false,

      alipayPayDialog1: false,
      alipayPayDialog2: false,
      alipayPayDialog3: false,
      cateList: [],
      payResult: {}
    };
  },
  methods: {
    refreshPrice() {
      //console.log(this.payState);
      this.$czapi
        .refreshPrice({
          token: this.payState.token
        })
        .then(data => {
          this.payData.price = data.data;
          this.$message.success("");
          this.getQRcode();
        });

      //
      /*var data = {
                              "code": 200,
                              "msg": "",
                              "data": "100"
                          };
                          vm.payData.price = data.data;
                          vm.$message.success("");*/
    },
    getQRcode() {
      this.$czapi
        .pay({
          product_id: this.payData.id,
          channel: "ums"
        })
        .then(data => {
          this.payResult = data;
        });
    },
    pay() {
      var vm = this;
      if (vm.payStyle === "yhk") {
        vm.yhkPayDialog = true;
        return false;
      }
      this.$czapi
        .pay({
          product_id: this.payData.id,
          channel: this.payStyle
        })
        .then(data => {
          if (data.code !== 200) {
            this.$modal.error({
              title: "",
              content: data.msg
            });
            return false;
          }

          if (vm.payStyle === "wechat") {
            vm.wechatPayDialog = true;
          }

          this.payResult = data;
          //console.log(data, "pay");
          //this.$nextTick(() => {
          //   if (this.payStyle === "alipay") {
          //     this.alipayPayDialog1 = true;
          //     // vm.alipayPayDialog2 = true;
          //     // vm.alipayPayDialog3 = true;
          //     this.openNew(
          //       `http://aci-api.chaozhiedu.com/api/pay/alipay/${
          //         this.payState.token
          //       }`
          //     );
          //     // let form = $(data.form)[0];
          //     // $("body").append(form);
          //     // form.submit();
          //     // $(form).remove();
          //     //document.forms["alipaysubmit"].submit();
          //   }
          //});
        });

      /*console.log({
                              product_id: vm.payData.id,
                              channel: vm.payStyle
                          })*/
    },

    wechatPayDialogCancle() {
      this.wechatPayDialog = false;
    },

    yhkPayDialogCancle() {
      this.yhkPayDialog = false;
    },

    alipayPayDialogCancle1() {
      this.alipayPayDialog1 = false;
    },
    alipayPayDialogCancle2() {
      this.alipayPayDialog2 = false;
    },
    alipayPayDialogCancle3() {
      this.alipayPayDialog3 = false;
    },
    getPayResult(token) {
      this.$czapi
        .getPayResult({
          token
        })
        .then(({ code, msg }) => {
          if (code != 200) {
            timer = setTimeout(() => {
              this.getPayResult(token);
            }, 3000);
            return false;
          }

          this.alipayPayDialog2 = true;
          //this.alipayPayDialog1 = false;
          this.wechatPayDialog = false;
        });
    }
  },
  mounted() {},
  activated() {
    this.payResult = {};
    let { id } = this.$route.query;
    if (id) {
      window.payData = {
        id
      };
    }
    console.log(window.payData);
    if (window.payData) {
      this.payData = window.payData;
      this.$czapi
        .getPayInfo({
          product_id: this.payData.id
        })
        .then(data => {
          this.payData = data.product;
          this.payState = data;
          this.payData.price = data.price;
          this.getPayResult(data.token);
          this.getQRcode();
        });
    } else {
      this.$router.push({
        name: "Male"
      });
    }
  },
  deactivated() {
    clearTimeout(timer);
  }
};
</script>

<style scoped>

</style>



