<template>
    <div class="login">
        <div class="container">
            <div class="login-container" style="margin-top: 40px;">
                <div class="text-center">
                    <img src="static/images/logo-3.png" alt="" style="width: 300px;">
                </div>
                <div class="text-center" style="margin: 20px 0;font-size: 26px;">
                    
                </div>

                <div style="width: 360px;margin: 0 auto;">
                    <el-form direction="horizontal" label-width="labelCol" :model="ruleForm" :rules="rules" ref="ruleForm">
                        <el-form-item label="" prop="phoneNumber">
                            <el-input placeholder="" v-model="ruleForm.phoneNumber">
                                <!-- <v-icon slot="before">
                                    <i class="iconfont icon-shoujihao" style="font-size: 22px;"></i>
                                </v-icon> -->
                            </el-input>
                        </el-form-item>

                        <el-form-item label="" prop="qcode">
                            <div class="inline-block text-center" style="width: 55%;">
                                <el-input placeholder="" v-model="ruleForm.qcode">
                                    <!-- <v-icon type="123" slot="before">
                                        <i class="iconfont icon-unie615" style="font-size: 22px;"></i>
                                    </v-icon> -->
                                </el-input>
                            </div>
                            <div class="inline-block text-center" style="width: 43%;">
                                <a href="javascript:;" class="link" @click="getQcode">{{qcodeText}}</a>
                            </div>
                        </el-form-item>

                        <el-form-item>
                            <el-button type="primary" :loading="isLoading" :disabled='!ruleForm.isTy' html-type="submit"
                                      @click.prevent="submitForm('ruleForm')"
                                      style="display: block;width: 100%;font-size: 22px;"></el-button>
                        </el-form-item>

                        <el-form-item>
                            <el-checkbox v-model="ruleForm.isTy"><a href="javascript:;" class='link' @click.prevent="visible = true"></a></el-checkbox>
                        </el-form-item>

                        <el-form-item>
                            <div class="text-center">
                                <a href="/default.html">
                                    <i class="iconfont icon-return"></i>
                                    <span style="margin-left: 5px;"></span>
                                </a>
                            </div>
                        </el-form-item>
                    </el-form>
                </div>


                <el-dialog title=""
                         :visible.sync="visible"
                         width="800">
                    <div style="height: 430px;overflow: auto;">1<br/>
                        <br/>
                        2<br/>
                        <br/>
                        1<br/>
                        2,<br/>
                        1)<br/>
                        2)<br/>
                        3<br/>
                        <br/>
                        4<br/>
                        <br/>
                        <br/>
                        <br/>
                        <br/>
                        6<br/>
                        <br/>


                        7/<br/>
                        <br/>
                        (1)<br/>
                        (2)<br/>
                        <br/>
                        8<br/>
                        <br/>
                        <br/>
                        <br/>
                        9<br/>
                        /<br/>
                        10<br/>
                        <br/>
                        <br/>
                        <br/>
                        <br/>
                        <br/>
                        <br/>
                    </div>

                </el-dialog>
            </div>

            <div class="text-center">
                <!--   -->
                  
                <!-- ICP17033346 -->
            </div>
        </div>
    </div>
</template>

<script>
var isMobileNum = function(num) {
  return /^(1[3-8]\d{9})$/.test(num);
};

var isLoginQcode = function(n) {
  return /^\d{4}$/.test(n);
};

var validatePhoneNumber = function(rule, value, callback) {
  if (isMobileNum(value)) {
    callback();
  } else {
    callback(new Error(""));
  }
};

var validateQcode = function(rule, value, callback) {
  if (isLoginQcode(value)) {
    callback();
  } else {
    callback(new Error(""));
  }
};
import { mapActions } from "vuex";
export default {
  name: "Login",
  data() {
    return {
      isLoading: false,
      labelCol: {
        span: 6
      },
      wrapperCol: {
        span: 24
      },
      ruleForm: {
        phoneNumber: "",
        qcode: "",
        isTy: true
      },
      rules: {
        phoneNumber: [
          {
            required: true,
            message: ""
          },
          {
            validator: validatePhoneNumber
          }
        ],
        qcode: [
          {
            required: true,
            message: ""
          },
          {
            validator: validateQcode
          }
        ]
      },
      visible: false,

      qcodeText: "",
      //60
      isSend: false
    };
  },
  methods: {
    ...mapActions({
      getUserInfo: "USER_INFO",
      getCateList: "CATE_LIST"
    }),
    //
    handleOk: function() {
      this.visible = false;
      this.ruleForm.isTy = true;
    },

    //
    handleCancel: function() {
      this.visible = false;
    },

    //
    getQcode: function() {
      var vm = this;
      //console.log(vm.isSend);
      if (!vm.isSend) {
        vm.isSend = true;

        //
        this.$refs.ruleForm.validateField("phoneNumber", a => {
          if (a === "") {
            vm.$czapi
              .getPhoneCaptcha({
                phone: vm.ruleForm.phoneNumber
              })
              .then(data => {
                if (data.code != 200) {
                  return this.$message.error(data.msg);
                }

                vm.isSend = true;
                var count = data.interval,
                  timer = null;
                vm.qcodeText = count + "";
                timer = setInterval(function() {
                  count--;
                  if (count > 0) {
                    vm.qcodeText = count + "";
                  } else {
                    vm.isSend = false;
                    vm.qcodeText = "";
                    clearInterval(timer);
                  }
                }, 1000);
              })
              .fail(function() {
                vm.isSend = false;
              });
          } else {
            vm.isSend = false;
          }
        });
      }
    },
    submitForm(formName) {
      //console.log(formName);
      this.$refs[formName].validate(valid => {
        if (valid) {
          //alert('!');
          this.isLoading = true;
          this.$czapi
            .doLogin({
              phone: this.ruleForm.phoneNumber,
              captcha: this.ruleForm.qcode
            })
            .then(data => {
              if (data.code != 200) {
                this.isLoading = false;
                this.$message.error(data.msg);
                return false;
              }

              //var expiresDate = new Date(data.expired);
              //cookietoken
              // $.cookie("userToken", data.token, {
              //   path: "/", //cookie
              //   expires: expiresDate
              // });
              //datatoken
              this.$storage.set("userToken", data);

              this.getUserInfo();
              this.getCateList();

              //
              //this.$router.push({ name: "Index" });
              this.$router.back();
              // 
              //vm.$router.push('/home/first')

              // 
              //vm.$router.push({ path: '/home/first' })

              // 
              //vm.$router.push({ name: 'home', params: { userId: wise }})
            })
            .catch(() => {
              this.isLoading = false;
            });

          setTimeout(() => {
            this.isLoading = false;
          }, 20000);
        } else {
          return false;
        }
      });
    }
  },
  mounted() {
    //console.log(this.$router);
  }
};
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>

</style>



