import { mapState } from "vuex";

export default {
  name: "Info",
  data() {
    return {
			steps:0,
      param: {
        cn_name: "", //
        en_name: "", //
        sex: "", // F  M 
        birthday: "", //
        college: "", //
        edu_num: "", //
        idcard: "", //,
        email: "",
        contacts_phone: "", //
        addr: "", //
        period: "", //ID
        idcard_front: "", // ID
        idcard_reverse: "", // ID
        edu: "", //ID
        degree: "", //ID
        entry_form: "", //ID
        avatar: "", //ID 
        idcard_front_file: "", //
        idcard_reverse_file: "", //
        edu_file: "", //
        degree_file: "", //
        entry_form_file: "", //
        avatar_file: "" //
      },
      // info: {
      //   user: {},
      //   ext_info: {},
      //   period: []
      // },

      sexType: [
        {
          value: "M",
          label: "M"
        },
        {
          value: "F",
          label: "F"
        }
      ],
      ksType: [],
      fileName: "file",
      uploadAction: "http://aci-api.chaozhiedu.com/api/file/upload",
      //cateList: [],
      // uploads: {
      //   idcard_front_file: "", //
      //   idcard_reverse_file: "", //
      //   edu_file: "", //
      //   degree_file: "", //
      //   entry_form_file: "", //
      //   avatar_file: "" //
      // }
      fileList: []
    };
  },
  computed: {
    ...mapState({
      info: state => state.userInfo
      //cateList: state => state.cateList
    })
  },
  watch: {
    info(val, old) {
      //console.log(val, "112312312");
      this.param = { ...this.param, ...val.ext_info, ...val.user };
      this.ksType = val.period;
    }
  },
  methods: {
    
    disabledDate(current) {
      return current && current.valueOf() >= Date.now();
    },
    
    onChangeIdCardZm(info) {
      if (info.file.status !== "uploading") {
        console.log(info.file, info.fileList);
      }
      if (info.file.status === "done") {
        console.log(info.file.name + " .");
      } else if (info.file.status === "error") {
        console.log(info.file.name + " .");
      }
    },
    triggerUpload(type) {
      console.log(type);
      document.getElementById("wtFile").click();
    },
    showConfirmForm() {
      this.$confirm(
        "\n" + "",
        "",
        {
          confirmButtonText: "",
          cancelButtonText: ""
        }
      )
        .then(() => {
          console.log("");
          let params = {};

          for (const key in this.param) {
            if (this.param.hasOwnProperty(key)) {
              const element = this.param[key];
              if (element) {
                params[key] = element;
              }
            }
          }
          //
          this.$czapi
            .submitUserInfo(params)
            .then(({ code, msg, data_msg = [] }) => {
              if (code != 200) {
                let keys = Object.keys.call(this, data_msg);
                if (keys.length > 0) {
                  this.$notify.error({
                    title: "",
                    message: data_msg[keys[0]]
                  });
                } else {
                  this.$message.error(msg);
                }
                return false;
              }

              this.$message({ message: msg, type: "success" });
            });
        })
        .catch(() => {});
    },

    //
    doSave() {
      let params = {};

      for (const key in this.param) {
        if (this.param.hasOwnProperty(key)) {
          const element = this.param[key];
          if (element) {
            params[key] = element;
          }
        }
      }

      //
      this.$czapi.addUserInfo(params).then(({ code, msg, data_msg = [] }) => {
        if (code != 200) {
          let keys = Object.keys.call(this, data_msg);
          //console.log(this);

          if (keys.length > 0) {
            this.$notify.error({
              title: "",
              message: data_msg[keys[0]]
            });
          } else {
            this.$message.error(msg);
          }
          return false;
        }

        this.$message.success(msg);
      });
    },
    onBeforeUpload(file, filename) {
      // console.log(file, reqOptions, ccc);
      //let { token } = this.$storage.get("userToken");

      let headers = {
          Token: this.Token
        },
        formData = new FormData();

      formData.append("file", file);

      fetch(this.uploadAction, {
        method: "POST",
        headers,
        body: formData
      })
        .then(res => {
          return res.json();
        })
        .then(res => {
          //console.log(data, file, reqOptions, 111);
          if (res.code == 200) {
            //console.log(reqOptions, this.$refs);
            this.param[filename] = res.data.url;
            this.param[filename.replace("_file", "")] = res.data.id;
            //console.log(this.param);
            this.$message.success("");
          }
        });

      this.fileList = [];
      return false;
    }
  },
  mounted() {
    this.param = { ...this.param, ...this.info.ext_info, ...this.info.user };
    this.ksType = this.info.period;
